<template>
  <div class="articleedit">
    <div class="edittitle clearfix">
      <div class="title">
        <input v-model="article.title" class="txt" type="text" placeholder="标题">
        <Select v-model="article.tag" style="width:100px" placeholder="标签">
          <Option v-for="item in getTagList.tagList" :value="item.tagName" :key="item">{{ item.tagName }}</Option>
        </Select>
      </div>
      <div class="handle">
        <Button>私密发布</Button>
        <Button type="primary" @click="articleSub">公开发布</Button>
      </div>
    </div>
    <div class="editcontent">
      <div class="editbox">
        <textarea class="articlecont" v-model="article.content"></textarea>
      </div>
      <div class="previewbox" v-html="compiledMarkdown">
      </div>
    </div>

  </div>
</template>

<script>
import Marked from 'marked';
import highlight from 'highlight.js'
import 'highlight.js/styles/googlecode.css'
import { mapGetters , mapActions } from 'vuex'

export default {
  components: {Marked},
  data () {
    return {
      input: '# hello',
      article:{
        title:'',
        content:'',
        tag:'',
      }
    }
  },
  computed:{
    ...mapGetters({
      getArticle: 'getArticle',
      getTagList: 'getTagList',
    }),
    ...mapActions({
      addArticle: 'addArticle',
      tagList: 'tagList',
      anArticle:'anArticle'
    }),
    compiledMarkdown: function () {
       return Marked(this.article.content, { sanitize: true })
     }
  },
  methods: {
    articleSub(){
      this.$store.dispatch('addArticle', this.article);
    }
  },
  mounted() {
    this.$store.dispatch('tagList');
    // const id = this.$route.query.id;
    // if(id){
    //   this.$store.dispatch('anArticle', id);
    // }

    // highlight.initHighlighting()
    // this.$store.dispatch('anArticle',(res)=>{
    //   console.log('article', res);
    // })

  },
  watch(){

  }
}
</script>
<style>
body{
color:#444;
font-family:Georgia, Palatino, 'Palatino Linotype', Times, 'Times New Roman', serif;
font-size:12px;
line-height:1.5em;
}
a{ color: #0645ad; text-decoration:none;}
a:visited{ color: #0b0080; }
a:hover{ color: #06e; }
a:active{ color:#faa700; }
a:focus{ outline: thin dotted; }
a:hover, a:active{ outline: 0; }

::-moz-selection{background:rgba(255,255,0,0.3);color:#000}
::selection{background:rgba(255,255,0,0.3);color:#000}

a::-moz-selection{background:rgba(255,255,0,0.3);color:#0645ad}
a::selection{background:rgba(255,255,0,0.3);color:#0645ad}

p{
margin:1em 0;
}

img{
max-width:100%;
}

h1,h2,h3,h4,h5,h6{
font-weight:normal;
color:#111;
line-height:1em;
}
h4,h5,h6{ font-weight: bold; }
h1{ font-size:2.5em; }
h2{ font-size:2em; }
h3{ font-size:1.5em; }
h4{ font-size:1.2em; }
h5{ font-size:1em; }
h6{ font-size:0.9em; }

blockquote{
color:#666666;
margin:0;
padding-left: 3em;
border-left: 0.5em #EEE solid;
}
hr { display: block; height: 2px; border: 0; border-top: 1px solid #aaa;border-bottom: 1px solid #eee; margin: 1em 0; padding: 0; }
pre, code, kbd, samp { color: #000; font-family: monospace, monospace; _font-family: 'courier new', monospace; font-size: 0.98em; }
pre { white-space: pre; white-space: pre-wrap; word-wrap: break-word; }

b, strong { font-weight: bold; }

dfn { font-style: italic; }

ins { background: #ff9; color: #000; text-decoration: none; }

mark { background: #ff0; color: #000; font-style: italic; font-weight: bold; }

sub, sup { font-size: 75%; line-height: 0; position: relative; vertical-align: baseline; }
sup { top: -0.5em; }
sub { bottom: -0.25em; }

ul, ol { margin: 1em 0; padding: 0 0 0 2em; }
li p:last-child { margin:0 }
dd { margin: 0 0 0 2em; }

img { border: 0; -ms-interpolation-mode: bicubic; vertical-align: middle; }

table { border-collapse: collapse; border-spacing: 0; }
td { vertical-align: top; }

@media only screen and (min-width: 480px) {
body{font-size:14px;}
}

@media only screen and (min-width: 768px) {
body{font-size:16px;}
}

@media print {
  * { background: transparent !important; color: black !important; filter:none !important; -ms-filter: none !important; }
  body{font-size:12pt; max-width:100%;}
  a, a:visited { text-decoration: underline; }
  hr { height: 1px; border:0; border-bottom:1px solid black; }
  a[href]:after { content: " (" attr(href) ")"; }
  abbr[title]:after { content: " (" attr(title) ")"; }
  .ir a:after, a[href^="javascript:"]:after, a[href^="#"]:after { content: ""; }
  pre, blockquote { border: 1px solid #999; padding-right: 1em; page-break-inside: avoid; }
  tr, img { page-break-inside: avoid; }
  img { max-width: 100% !important; }
  @page :left { margin: 15mm 20mm 15mm 10mm; }
  @page :right { margin: 15mm 10mm 15mm 20mm; }
  p, h2, h3 { orphans: 3; widows: 3; }
  h2, h3 { page-break-after: avoid; }
}
</style>



